const leagues = {
  "football":"https://soccerstreams-100.com/wp-json/addp/",
  "american-football":"https://www.nflstreams100.com/wp-json/addp/",
  "basketball":"https://www.nbastreams100.com/wp-json/addp/",
  "baseball":"https://www.mlbstreams100.com/wp-json/addp/",
  "ice-hockey":"https://www.nhlstreams100.com/wp-json/addp/",
}
class WebService {
  static getEvents(date,league) {
    return new Promise((resolve, reject) => {
      axios.get(`https://api.sofascore.com/api/v1/sport/${league}/scheduled-events/${date}`).then(function functionName(res) {
        const data = res.data.events.filter(x => {
          return soccer.map(x=>{return x.uid}).includes(x.tournament.uniqueTournament?.id);
        });
        let dat = [];
        data.forEach(x => {
          let z = x
          z.tournamentName = x.tournament.uniqueTournament.name
          z.name = `${x.awayTeam.name} at ${x.homeTeam.name}`
          z.time = Moment(x.startTimestamp*1000).format('MM/DD  HH:mm')
          dat.push(z);
        });
        resolve(
          dat.length > 0 ? dat.sort(function(a, b){return a.startTimestamp - b.startTimestamp}) : [{
            name: 'There is no events for this date , please select another date',
            customId: 'error'
          }]
        );
      }).catch((err) => {
        reject(err)
      })
    });
  }
  static insertGame(game,i,league) {
    return new Promise((resolve, reject) => {
      axios.post(leagues[league]+soccer.find(x=>{return x.uid === i}).id, {
        game: game
      }).then(function(res) {
        resolve(res.data)
      }).catch((err) => {
        reject(err)
      })
    });
  }
}

export default WebService;
