<template>
<section>
    <table >
       <tbody>
         <tr v-for="item in tournaments" v-bind:key="item.customId">
           <td v-if="item.customId !== 'error'" style="text-align: center">{{item.time}}</td>
           <td v-if="item.customId === 'error'" style="text-align: center">{{item.name}}</td>
           <td v-if="item.customId !== 'error'">{{item.name}}</td>
           <td v-if="item.customId !== 'error'" style="text-align: center">{{item.tournamentName}}</td>
           <td v-if="item.customId !== 'error'">
             <span v-if="!submitted.includes(item.customId)" class="sub" v-on:click="submit(item,item.tournament.uniqueTournament.id)">Submit</span>
             <span v-if="submitted.includes(item.customId)" class="n sub">Submitted
               <button v-on:click="clear(item.customId)">
                 <span>&#10007;</span>
               </button>
             </span>
           </td>
         </tr>
       </tbody>
    </table>
</section>
</template>

<script>
import WebService from "../WebService";

export default {
  name: 'Events',
  data (){
    return {
      submitted: [],
      error: ''
    }
  },
  props: {
    tournaments: {
      type: Array
    },
    league: {
      type: String
    }
  },
  mounted() {
    if (localStorage.getItem('submitted'))
    this.submitted = JSON.parse(localStorage.getItem('submitted'));
  },
  methods:{
    submit:function (item,i) {
      let vm = this;
      WebService.insertGame(item,i,vm.league).then(() =>{
        this.submitted.push(item.customId);
        localStorage.setItem('submitted', JSON.stringify(this.submitted));
      }).catch(err=>{
        this.error = err.message;
        alert('there is an error please try again');
      })
    },
    clear:function (item) {
      this.submitted.splice(this.submitted.indexOf(item),1);
      localStorage.setItem('submitted', JSON.stringify(this.submitted));
    }
  }
}
</script>
<style scoped>
button{
  right: 2px;
  top: 2px;
  width: 28px;
  height: 28px;
  cursor: pointer;
}
section{
  background-color: #edeef0;
    min-height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    font-size: calc(5px + 2vmin);
    color: #282828;
    width: 100%
}
